html {
	background-color: $viewport-background-color;
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
	height: 100%;
}

body {
	margin: 0;
}




// Page Sections
%page-section {
	width: 100%;
	display: block;
	position: relative;
	overflow: hidden;
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	box-sizing: border-box;
}

.page-section {
	@extend %page-section;
	padding: 60px 0;
	@include mq($min-width: 480px) {
		padding-top: 80px;
		padding-bottom: 80px;
	}
	@include mq($min-width: 768px) {
		padding-top: 120px;
		padding-bottom: 120px;
	}
	@include mq($min-width: 1024px) {
		padding-top: 140px;
		padding-bottom: 140px;
	}
}

.small-section {
	@extend %page-section;
	padding: 30px 0;
	@include mq($min-width: 480px) {
		padding-top: 50px;
		padding-bottom: 50px;
	}
	@include mq($min-width: 768px) {
		padding-top: 70px;
		padding-bottom: 70px;
	}
}

.certification_licensing {
	text-align: center;
	@include media-breakpoint-up(md) {
		text-align: left;
	}
}